.landing-block {
    padding: 40px 0 64px;
    border-bottom: 1px solid #484848;
    @media (min-width: 992px) {
        padding: 30px 0 80px;
    }

    &__info {
        display: flex;
        flex-direction: column;
        justify-content: center;
        margin-bottom: 64px;
        @media (min-width: 992px) {
            margin-bottom: 0px;
        }
    }

    &__title {
        font-family: 'Bebas Neue';
        font-size: 57px;
        font-weight: 400;
        line-height: 57px;
        color: #FFF;
        text-transform: uppercase;
        @media (min-width: 576px) {
            font-size: 101px;
            line-height: 91px;
        }
    }

    &__description {
        p {
            font-size: 16px;
            font-weight: 400;
            line-height: 26px;
            color: #C7C7C7;
            margin-bottom: 32px;
            @media (min-width: 576px) {
                font-size: 18px;
                line-height: 27px;
                margin-bottom: 40px;
            }
        }
    }

    &__links {
        display: flex;
        align-items: center;
        gap: 16px;

        a {
            text-decoration: none;
        }

        .btn {
            background: #D3E97A;
            border-radius: 100px;
            padding: 4px 6px 4px 24px;
            display: flex;
            justify-content: center;
            align-items: center;
            gap: 12px;
            font-size: 16px;
            font-weight: 700;
            line-height: 16px;
            color: #0A0A0A;
            text-transform: uppercase;
        }
    }

    &__image {
        img {
            width: 100%;
        }
    }
}